.main-features {
  width: 260px;

  margin: 0 auto;
  padding: 31px 0 29px;

  @media (min-width: @tablet-width) {
    width: 668px;

    margin: 0 auto;
    padding: 55px 0 83px;
  }

  @media (min-width: @desktop-width) {
    width: 1050px;
    padding: 68px 0 90px;
    margin: 0 auto;
  }
}

.main-features__heading {
  margin: 0;
  margin-bottom: 28px;

  @media (min-width: @tablet-width) {
    margin-bottom: 40px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 175px;
  }
}

.main-features__list {
  display: grid;
  row-gap: 30px;

  margin: 0;
  padding: 0;

  @media (min-width: @tablet-width) {
    grid-template-columns: 325px 325px;
    column-gap: 10px;
  }

  @media (min-width: @desktop-width) {
    column-gap: 25px;
    padding-left: 175px;
  }
}

.main-features__item {
  padding-top: 54px;

  list-style-type: none;

  font-weight: 700;
  font-size: 17px;
  line-height: 20px;

  background-repeat: no-repeat;

  &--eco {
    background-image: url("../img/eco.svg");
    background-size: 33px 38px;

    @media (min-width: @tablet-width) {
      order: 1;
    }
  }

  &--money {
    background-image: url("../img/money.svg");
    background-size: 47px 27px;

    @media (min-width: @tablet-width) {
      order: 3;
    }
  }

  &--heart {
    background-image: url("../img/heart.svg");
    background-size: 34px 27px;

    @media (min-width: @tablet-width) {
      order: 5;
    }
  }

  &--clew {
    background-image: url("../img/clew.svg");
    background-size: 35px 34px;

    @media (min-width: @tablet-width) {
      order: 2;
    }
  }

  &--rocket {
    background-image: url("../img/rocket.svg");
    background-size: 55px 23px;

    @media (min-width: @tablet-width) {
      order: 4;
    }
  }

  &--gift {
    background-image: url("../img/gift.svg");
    background-size: 33px 32px;

    @media (min-width: @tablet-width) {
      order: 6;
    }
  }
}
